import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class UserService {
  baseUrl: string = "http://localhost:8080/";
  constructor(private httpClient:HttpClient) { }

  getUserList(){
    return this.httpClient.get(this.baseUrl+"userList");
  }
  saveUser(userData){
    return this.httpClient.post(this.baseUrl+"saveUser",userData);
  }
  getUserDetails(userId){
    return this.httpClient.get(this.baseUrl+"userDetails/"+userId);
  }
  updateUser(data){
    return this.httpClient.put(this.baseUrl+"updateUser",data);
  }
  deleteUser(userId){
    return this.httpClient.delete(this.baseUrl+"userDetails/"+userId);
  }
}
