import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from '../user.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.css']
})
export class EditUserComponent implements OnInit {
userId:any;
user:any;
userForm:FormGroup;
  constructor(private formBuilder: FormBuilder,private activatedRoute:ActivatedRoute,private router:Router, private userService :UserService) { }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe(params => {
      this.userId = params['userId'];
      this.getUserDetails(this.userId);
  });


}

getUserDetails(userId){
  this.userService.getUserDetails(userId).subscribe(res=>{
    this.user=res;

    this.userForm = this.formBuilder.group({
      firstName: [this.user.firstName, Validators.required],
      lastName: [this.user.lastName, Validators.required],
      email: [this.user.email, Validators.required],
      userPwd: ['', Validators.required]
    });
  })
}

onSubmit(){
  console.log("value--"+this.userForm.value);
  let data=this.userForm.value;
  data.userId=this.userId;
  this.userService.updateUser(data).subscribe(res=>{
    console.log("value=="+res);
    this.router.navigate(['userList']);
});
}
cancel(){
  this.router.navigate(['userList']);
}
}
